      handleSizeChange(val) {
        this.pageSize = val
        this.fetchData(val, this.currentPage)
      },

      /**
       * 
       * @param val  
       */
      handleCurrentChange(val) {
        this.checkedAll = ''
        this.currentPage = val
        this.fetchData(this.pageSize, val)
      },

      /**
       * 
       * @param row 
       */
      showDetail(row) {
        this.currentItem  = row
        var status = Number(row.status)
        var isRepay = Number(row.isRepay)
        if (isRepay == 1 && status == 0) { //
          this.sureStatus = ''
          this.paymentDetailBtn2 = true
          this.sureStatusMsg = ''
        } else if (isRepay == 1 && status == 2) { //  
          this.sureStatus = ''
          this.sureStatusMsg = ''
          this.paymentDetailBtn2 = true
        } else if (isRepay == 1 && status == 3) {  //  
          this.sureStatus = ''
          this.sureStatusMsg = ''
          this.paymentDetailBtn2 = true
        } else if (isRepay == 0 && status == 5) { //
          this.sureStatus = ''
          this.paymentDetailBtn = true
        }
        this.confirmReceipt()

        var {id, contractId} = row
        var _this = this
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.feeDetails,
          data: {
            id: id,
            contractId: contractId
          }
        }).then(function (response) {
          const {data} =response
          if(data.status.code == 200){
            _this.viewDetailData = response.data.result
          }
        })
      },

      /**
       * 
       */
      search() {
        this.fetchData(this.pageSize, this.currentPage)
      },

      /**
       *  ()
       * @param item
       */
      showContractPlan(item) {
        var {gcid, contractId} = item
        this.ContractPlanBtn = true
        var _this = this
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeReriods,
          data: {
            gcid,
            contractId
          }
        }).then(function (response) {
          var {data} = response
          if (data.status.code == 200) {
            _this.ContractPlanMsg = response.data.result
          }
        }).catch(error => {
          this.ContractPlanBtn = false
        })
      },

      /**
       * 
       * @param item 
       */
      stateMsg(item) {
        var num = '';
        var str = '';
        var classNum = ''
        var obj = {}
        num = Number(item.status)
        if (num == 3 || num == 0) {
          str = ''
          classNum = 3
        } else if (num == 1) {
          str = ''
          classNum = 1
        } else if (num == 2) {
          str = ''
          classNum = 2
        } else {
          str = ''
          classNum = 4
        }
        return {
          str,
          classNum
        };
      },

      /**
       * 
       */

      /**
       * 
       * @param item 
       */
      handleDo(item) {
        const { selectedIndex} = this.listResult.financeList;
        // 
        this.currentItem.exemptLateFee = ''
        this.currentItem.exemptLateFeeRemark = '';
        this.currentItem = item.row;
        this.currentItemIndex =item.$index;
        var val = item.row.selectedIndex;
        if (val == '') {
          this.confirmReceiptInputBtn = true
        } else if (val == '') {
          this.paymentRejectBtn = true
        } else if (val == '' || val == '') {
          this.confirmReceiptBtn = true
          this.confirmReceipt()
        } else if (val == '' || val == '') {
          this.breaksMoneyBtn = true
        } else if (val == '') {
          this.smsNotice()
        }else if(val ==''){
          this.paymentDetailBtn = true
          this.showDetail(this.currentItem)
        }
      },
      smsNotice() {
        var ids = [];
        this.paymentDetailBtn = false
        var id = this.currentItem.id;
        ids.push(id)
        this.smsNoticeFn(ids)
      },
      smsNoticeFn(ids){
        var _this = this
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.smsNotice,
          data: {
            ids:ids
          }
        }).then((response) => {
          const {data} = response;
          if (data.status.code == 200) {
            _this.$message({
              message: '',
              type: 'success',
              duration: 1000
            });
           // _this.reload()
          }
        }).catch(error => {
          _this.loading.back = false;
        })
      },
      /**
       *  
       */
      passDialog(val) {
        if (val == 1) {
          this.paymentRejectBtn = true
        } else {
          this.confirmReceiptInputBtn = true;
        }
        this.confirmReceiptBtn = false
      },

      /**
       *   
       */
      pass() {
        const self = this;
        const userid = tool.getUserId();
        if (!userid) {
          self.$message.error("");
          return;
        }
        var _this = this
        var remark = this.confirmReceiptForm.msg;
        var confirmTime = this.confirmReceiptForm.date
        var {id, gcid} = this.confirmReceiptFormDialog.financeBill
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeAffirmPrice,
          data: {
            id,
            gcid,
            remark,
            userid,
            confirmTime
          }
        }).then((response) => {
          const {data} = response;
          if (data.status.code == 200) {
            _this.loading.sure = false;
            _this.paymentRejectBtn = false
            _this.confirmReceiptInputBtn = false
            _this.confirmReceiptForm.msg =''
            _this.$message({
              message: '',
              type: 'success',
              duration: 1000
            });
            _this.reload()
          }
        }).catch(error => {
          _this.loading.sure = false;
        })
      },

      /**
       *   
       */
      reject() {
        var _this = this;
        this.paymentRejectBtn = false;
        this.loading.back = true;
        var {id, gcid} = this.currentItem;
        var rejectReason = this.rejectForm.msg;
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeReject2,
          data: {
            id,
            gcid,
            rejectReason
          }
        }).then((response) => {
          _this.loading.back = false;
          const {data} = response;
          if (data.status.code == 200) {
            this.paymentRejectBtn = false
            _this.$message({
              message: '',
              type: 'success',
              duration: 1000
            });
            _this.reload()
          } else {
            _this.$message({
              showClose: true,
              message: data.status.msg,
              type: 'error',
              duration: 1000
            });
          }
        }).catch(error => {
          _this.loading.back = false;
        })
      },

      /**
       * 
       */
      reject2() {
        var _this = this;
        this.paymentRejectBtn = false;
        this.loading.back = true;
        var {id, gcid} = this.currentItem;
        var rejectReason = this.rejectForm.msg;
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.financeReject2,
          data: {
            id,
            gcid,
            rejectReason
          }
        }).then((response) => {
          _this.loading.back = false;
          const {data} = response;
          if (data.status.code == 200) {
            this.paymentRejectBtn = false
            _this.$message({
              message: '',
              type: 'success',
              duration: 1000
            });
            _this.reload()
          } else {
            _this.$message({
              showClose: true,
              message: data.status.msg,
              type: 'error',
              duration: 1000
            });
          }
        }).catch(error => {
          _this.loading.back = false;
        })
      },

      /**
       *   ()
       */
      confirmReceipt() {
        var _this = this
        var {id, contractId} = this.currentItem;
        var msg = this.rejectForm.msg;
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.affirmMessage,
          data: {
            id,
            contractId
          }
        }).then((response) => {
          var {data} = response
          if (data.status.code == 200) {
            data.result.pictrues.forEach((item,index)=>{
              Object.assign(item,{
                w: 800,
                h: 600
              })
            });
            _this.confirmReceiptFormDialog = data.result
          }
        })
      },
      //
      reload() {
        var currentPage = 1
        this.fetchData(this.pageSize, currentPage);
      },
      /**
       *  
       */
      revertMoney(val) {
        var _this = this
        var {id, gcid} = this.currentItem;
        var exemptLateFeeStatus = val
        var exemptLateFee = this.currentItem.exemptLateFee; //
        var lateFee = this.currentItem.lateFee  // 
        // 
        if (Number(lateFee) < Number(exemptLateFee)) {
          this.$message.error('')
          return
        }
        var exemptLateFeeRemark = this.currentItem.exemptLateFeeRemark

        var dataOption = {
          id,
          gcid,
          exemptLateFee,
          exemptLateFeeRemark,
          exemptLateFeeStatus
        }
        /*this.loading.breaks = true*/
        this.axios({
          method: 'post',
          url: this.$store.state.api.finance.exemptLateFee,
          data: dataOption
        }).then((response) => {
          _this.loading.breaks = false;
          const {data} = response;
          if (data.status.code == 200) {
            // 
            _this.currentItem.exemptLateFee = ''
            _this.currentItem.exemptLateFeeRemark = ''
            _this.$alert("", "", {
              callback: action => {
                _this.fetchData(_this.pageSize, _this.currentPage);
                _this.breaksMoneyBtn = false
              }
            });
          }
        }).catch(error => {
          _this.loading.breaks = false;

        });
      },
      /**
       *  
       */
      breaksMoney() {

      },
      //
      stopPor() {
      },
      handleSelectionChange(val) {
        var {id} = val[0]
        this.selectList.push(id)
      }
    }
  };
</script>

