            </el-col>
            <el-col :span="8">
              <span class="detail-title"></span>
              <span class="detail-item" v-text="dialog.detail_msg.contractCt"></span>
            </el-col>
            <el-col :span="8">
              <span class="detail-title"></span>
              <span class="detail-item" v-text="dialog.detail_msg.contractNo"></span>
            </el-col>
