	</el-main>
</template>

<script>
export default {
  name: "AlreadyAdopted",
  data() {
    return {
      loading: {
        all: true, //loading
        detail: true //loading
      },
      imgList:[], //
      //
      currentPage: 1, //
      pageSize: 10, //
      totalRecord: 0, //

      formInline: {
        minPrice: "", //
        maxPrice: "", //
        date: "", // 
        likeName: "" //
      },

      dialog: {
        detail: false, //
        detail_msg: {} //
      },

      //
      table: {
        tbody: [],
        currentPage: 1,
        pageSize: 10,
        totalRecord: 0
      }
    };
  },
  methods: {
    /**---------- */
    openDetail(id) {
      this.dialog.detail = true;
      this.getDetail(id);
    },
    /**---------- */
    closeDetail() {
      this.dialog.detail = false;
    },

    /**---------- */
    getDetail(id) {
      const self = this;
      //loading
      self.loading.detail = true;
      self
        .axios({
          method: "post",
          url: this.$store.state.api.borrowApply.getBorrowApplyList,
          data: {
            id: id
          }
        })
        .then(response => {
          //loading
          self.loading.detail = false;
          const { data } = response;
          if (data.status.code == 200) {
            const { baseJsonObject, borroJsonObject } = data.result;
            var detail_msg = {
              id: id,
              borrowType: borroJsonObject.borrowType == 1 ? "" : "", //
              expectDate: borroJsonObject.expectDate, //
              expectPrice: borroJsonObject.expectPrice, //
              description: borroJsonObject.description, //
              apartmentType: borroJsonObject.apartmentType == 0 ? "" : "", //
              apartmentBrand: baseJsonObject.apartmentBrand, //
              borrowPerson: baseJsonObject.borrowPerson, //
              borrowPhone: baseJsonObject.borrowPhone, //
              apartmentAddress: baseJsonObject.apartmentAddress, //
              remark: borroJsonObject.remark, //
              hosueAddress: borroJsonObject.hosueAddress, //
              roomType: borroJsonObject.roomType, //
              area: borroJsonObject.area + "", //
              decoration: borroJsonObject.decoration, //
              payeeName: borroJsonObject.payeeName, //
              payeePhone: borroJsonObject.payeePhone, //
              idCard: borroJsonObject.idCard, //
              bank: borroJsonObject.bank, //
              bankAccount: borroJsonObject.bankAccount, //
              idcZmURL: data.result.idcardface.smallImgrul
                ? data.result.idcardface.smallImgrul
                : data.result.idcardface.bigImgurl, //
              idcFmURL: data.result.idcardback.smallImgrul
                ? data.result.idcardback.smallImgrul
                : data.result.idcardback.bigImgurl, //
              ContractImageURL:
                data.result.contractlist.length > 0
                  ? data.result.contractlist[0].smallImgrul
                  : "", //
              HouseCardURL: data.result.propertycertificate.smallImgrul
                ? data.result.propertycertificate.smallImgrul
                : data.result.propertycertificate.bigImgurl, //
              EntrustURL: data.result.ownerproxy.smallImgrul
                ? data.result.ownerproxy.smallImgrul
                : data.result.ownerproxy.bigImgurl, //
              checkName: borroJsonObject.checkName, //
              checkDate: borroJsonObject.checkDate, //
              approvalIdea: borroJsonObject.approvalIdea, //
              contractCt: borroJsonObject.contractCt, //
              contractCtName: borroJsonObject.contractCtName, //
              contractNo: borroJsonObject.contractNo //
            };
            this.imgList = [];
            this.imgList.push({  //
              u: data.result.idcardface.smallImgrul,
              bu: data.result.idcardface.bigImgurl,
              desc: ''
            });
            this.imgList.push({  //
              u: data.result.idcardback.smallImgrul,
              bu: data.result.idcardback.bigImgurl,
              desc: ''
            });
            this.imgList.push({  //
              u: data.result.contractlist.length > 0 ? data.result.contractlist[0].smallImgrul : '',
              bu: data.result.contractlist.length > 0 ? data.result.contractlist[0].smallImgrul : '',
              desc: ''
            });
            this.imgList.push({  //
              u: data.result.propertycertificate.smallImgrul,
              bu: data.result.propertycertificate.bigImgurl,
              desc: ""
            });
            this.imgList.forEach((item, index) => {
              Object.assign(item, {
                w: 800,
                h: 600
              })
            });
            if (borroJsonObject.borrowStatus == 1) {
              detail_msg.borrowStatusClass = "green";
              detail_msg.borrowStatusText = "";
            } else if (borroJsonObject.borrowStatus == 2) {
              detail_msg.borrowStatusClass = "red";
              detail_msg.borrowStatusText = "";
            }
            self.dialog.detail_msg = detail_msg;
          }
        })
        .catch(error => {
          self.loading.all = false;
        });
    },

    /**---------- */
    getBorrowApplyList() {
      const self = this;
      //loading
      self.loading.all = true;
      self
        .axios({
          method: "post",
          url: this.$store.state.api.borrowApply.getList,
          data: {
            minPrice: self.formInline.minPrice,
            maxPrice: self.formInline.maxPrice,
            pageNo: self.table.currentPage,
            pageSize: self.table.pageSize,
            likeName: self.formInline.likeName,
            startDate: self.formInline.date[0],
            endDate: self.formInline.date[1],
            status: 3 //0 1 2 3
          }
        })
        .then(response => {
          //loading
          self.loading.all = false;
          const { data } = response;
          if (data.status.code == 200) {
            self.table.tbody = [];
            //
            data.result.list.forEach(item => {
              self.table.tbody.push(item);
            });
            self.totalRecord = data.result.totalRecord;
          }
        })
        .catch(error => {
          self.loading.all = false;
        });
    },

    /**---------- */
    currentChange(currentPage) {
      const self = this;
      self.table.currentPage = currentPage;
      self.getBorrowApplyList();
    },

    /**---------- */
    sizeChange(pageSize) {
      const self = this;
      self.table.currentPage = 1;
      self.table.pageSize = pageSize;
      self.getBorrowApplyList();
    },

    /**---------- */
    tableRowClick(row) {
      const self = this;
      if (row && row.id) {
        const id = row.id;
        self.openDetail(id);
      }
    }
  },
  created() {
    var self = this;
    self.getBorrowApplyList();
  }
};
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}
.el-row.no-margin-bottom {
  margin-bottom: 0px;
}
.el-row.search {
  background: #fff;
  padding: 20px;
}
.el-button--primary {
  width: 100%;
  background-color: #188ff0;
  border-color: #188ff0;
  border-radius: 0px;
}
.el-button--primary.is-plain {
  background-color: #fff;
  border-color: #188ff0;
  border-width: 1px;
  color: #188ff0;
}
/deep/ .el-input__inner {
  border-radius: 0px;
}
/deep/ .el-button--primary span {
  font-size: 14px;
}
/deep/ .el-table tr {
  background: #f7f7f7;
}
/deep/ .el-table--striped .el-table__body tr.el-table__row--striped td {
  background: #ffffff;
}
/deep/ .el-table th > .cell {
  font-size: 14px;
}
/deep/ .el-table thead tr {
  background-color: #ffffff;
  color: #2e3445;
}
/deep/ .el-pagination {
  padding: 0px;
  text-align: right;
}
/deep/ .el-pagination.is-background .el-pager li.active {
  background-color: #188ff0;
}
/deep/ .el-pagination.is-background .el-pager li:hover {
  color: #188ff0;
}
/deep/ .el-form-item {
  margin: 0px;
  border: 1px solid #dcdfe6;
}
/deep/ .el-form-item .el-input__inner {
  border: 0px;
}
/deep/ .noMarginRight > .el-form-item__content {
  width: 100%;
}
/deep/ .el-form--inline .el-form-item {
  margin-right: 10px;
}
/deep/ .el-form--inline .el-form-item__label {
  padding-left: 12px;
  padding-right: 0px;
}
/deep/ .el-date-editor .el-range-input {
  width: 80px;
}
/deep/ .el-date-editor .el-range-separator {
  width: 30px;
}
/deep/ .el-dialog__title {
  color: #188ff0;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  font-size: 14px;
  color: #188ff0;
}
/deep/ .el-table {
  min-height: calc(100vh - 275px);
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  font-size: 24px;
}
.finance-state {
  display: inline-block;
  color: #fff;
  width: 100px;
  padding-right: 10px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background: url(../../../static/image/icon_state_green.png) left top no-repeat;
  background-size: 100px 30px;
}
.title {
  color: #188ff0;
  padding-left: 20px;
  position: relative;
  height: 20px;
  line-height: 20px;
  margin-bottom: 20px;
  font-weight: normal;
  font-size: 14px;
}
.title::before {
  content: "";
  display: block;
  width: 3px;
  height: 18px;
  background-color: #188ff0;
  position: absolute;
  top: 1px;
  left: 0px;
}
.title.no-mark {
  padding-left: 0px;
}
.title.no-mark::before {
  content: none;
}
.detail-title {
  font-size: 14px;
  color: #000;
  margin-right: 20px;
}
.detail-item {
  font-size: 12px;
  color: #777;
}
.detail-item.red {
  color: #f2082e;
}
.detail-item.green {
  color: #01be6a;
}
.division {
  border-style: solid;
  border-width: 1px;
  border-color: #dfe4e6;
  margin: 0px;
  margin-bottom: 20px;
}
.detail-img {
  display: block;
  border: 0px;
  width: 85%;
  height: 85%;
  margin: 0 auto;
}
.image_box figure {
  width: 100px;
  height: 94px;
  float: left;
  overflow: hidden;
  margin: 0;
  margin-left: 5px;
}
.image_box::after{
  content: '';
  display: block;
  clear: both;
}
/deep/ .image_box img{
  width: 100px;
  height: 60px  ;
}
/deep/ .image_box figcaption{
  text-align: center;
}
</style>



