<script>
import tool from "../../../static/js/tool.js";
export default {
  name: "WaitExaminaApproval",
  data() {
    var self = this;

    return {
      dialogType: "", // or 
      imgList:[], //
      loading: {
        all: true, //loading
        detail: true, //loading
        reject: false, //loading
        adopt: false //loading
      },

      dialog: {
        detail: false, //
        detail_msg: {}, //
        reject: false, //
        reject_msg: {}, //
        adopt: false, //
        adopt_msg: {} //
      },

      currentPage: 1, //
      pageSize: 10, //
      totalRecord: 0, //

      formInline: {
        minPrice: "", //
        maxPrice: "", //
        date: "", // 
        likeName: "" //
      },

      //
      screening: {
        areaValue: "", ///
        area: [] ///
      },

      //
      table: {
        tbody: [] //
      }
    };
  },
  methods: {
    /**---------- */
    openDetail(id) {
      this.dialog.detail = true;
      this.getBorrowApplyList(id);
    },
    openDetail2(id) {
      const self = this;
      self.dialogType = "adopt";
      self.openDetail(id);
    },
    /**---------- */
    closeDetail() {
      this.dialog.detail = false;
    },

    /**---------- */
    openReject(id) {
      this.dialog.reject_msg.id = id;
      this.dialog.reject = true;
    },
    /**---------- */
    closeReject() {
      this.dialog.reject_msg.id = "";
      this.dialog.reject_msg.content = "";
      this.dialog.reject = false;
    },

    /**---------- */
    openAdopt(id) {
      this.dialog.adopt_msg.id = id;
      this.dialog.adopt = true;
    },
    /**---------- */
    closeAdopt() {
      this.dialog.adopt_msg.id = "";
      this.dialog.adopt_msg.content = "";
      this.dialog.adopt = false;
    },

    /**---------- */
    getBorrowApplyList(id) {
      const self = this;
      //loading
      self.loading.detail = true;
      self
        .axios({
          method: "post",
          url: this.$store.state.api.borrowApply.getBorrowApplyList,
          data: {
            id
          }
        })
        .then(response => {
          //loading
          self.loading.detail = false;
          const { data } = response;
          if (data.status.code == 200) {
            const { baseJsonObject, borroJsonObject } = data.result;
            var detail_msg = {
              id: id,
              borrowType: borroJsonObject.borrowType == 1 ? "" : "", //
              expectDate: borroJsonObject.expectDate, //
              expectPrice: borroJsonObject.expectPrice, //
              description: borroJsonObject.description, //
              apartmentType:
                borroJsonObject.apartmentType == 0 ? "" : "", //
              apartmentBrand: baseJsonObject.apartmentBrand, //
              borrowPerson: baseJsonObject.borrowPerson, //
              borrowPhone: baseJsonObject.borrowPhone, //
              apartmentAddress: baseJsonObject.apartmentAddress, //
              remark: borroJsonObject.remark, //
              hosueAddress: borroJsonObject.hosueAddress, //
              roomType: borroJsonObject.roomType, //
              area: borroJsonObject.area + "", //
              decoration: borroJsonObject.decoration, //
              payeeName: borroJsonObject.payeeName, //
              payeePhone: borroJsonObject.payeePhone, //
              idCard: borroJsonObject.idCard, //
              bank: borroJsonObject.bank, //
              bankAccount: borroJsonObject.bankAccount, //
              idcZmURL: data.result.idcardface.smallImgrul
                ? data.result.idcardface.smallImgrul
                : data.result.idcardface.bigImgurl, //
              idcFmURL: data.result.idcardback.smallImgrul
                ? data.result.idcardback.smallImgrul
                : data.result.idcardback.bigImgurl, //
              ContractImageURL:
                data.result.contractlist.length > 0
                  ? data.result.contractlist[0].smallImgrul
                  : "", //
              HouseCardURL: data.result.propertycertificate.smallImgrul
                ? data.result.propertycertificate.smallImgrul
                : data.result.propertycertificate.bigImgurl, //
              EntrustURL: data.result.ownerproxy.smallImgrul
                ? data.result.ownerproxy.smallImgrul
                : data.result.ownerproxy.bigImgurl //
            };
            this.imgList = []
            this.imgList.push({  //
              u: data.result.idcardface.smallImgrul,
              bu: data.result.idcardface.bigImgurl,
              desc: ''
            });
            this.imgList.push({  //
              u: data.result.idcardback.smallImgrul,
              bu: data.result.idcardback.bigImgurl,
              desc: ''
            });
            this.imgList.push({  //
              u: data.result.contractlist.length > 0 ? data.result.contractlist[0].smallImgrul : '',
              bu: data.result.contractlist.length > 0 ? data.result.contractlist[0].smallImgrul : '',
              desc: ''
            });
            this.imgList.push({  //
              u: data.result.propertycertificate.smallImgrul,
              bu: data.result.propertycertificate.bigImgurl,
              desc: ""
            });
            this.imgList.forEach((item, index) => {
              Object.assign(item, {
                w: 800,
                h: 600
              })
            });
            console.log(this.imgList);
            self.dialog.detail_msg = detail_msg;
          }
        })
        .catch(error => {
          self.loading.all = false;
        });
    },

    /**---------- */
    getList() {
      const self = this;
      //loading
      self.loading.all = true;
      self
        .axios({
          method: "post",
          url: this.$store.state.api.borrowApply.getList,
          data: {
            pageNo: self.currentPage,
            minPrice: self.formInline.minPrice,
            maxPrice: self.formInline.maxPrice,
            pageSize: self.pageSize,
            likeName: self.formInline.likeName,
            startTime: self.formInline.date[0],
            endTime: self.formInline.date[1],
            status: 0 //0 1 2 3
          }
        })
        .then(response => {
          //loading
          self.loading.all = false;
          const { data } = response;
          if (data.status.code == 200) {
            self.table.tbody = [];
            //
            data.result.list.forEach(item => {
              self.table.tbody.push(item);
            });
            self.totalRecord = data.result.totalRecord;
          }
        })
        .catch(error => {
          self.loading.all = false;
        });
    },

    /**---------- */
    currentChange(currentPage) {
      const self = this;
      self.currentPage = currentPage;
      self.getList();
    },

    /**---------- */
    sizeChange(pageSize) {
      const self = this;
      self.currentPage = 1;
      self.pageSize = pageSize;
      self.getList();
    },

    /**---------- */
    submitReject() {
      const self = this;
      const userid = tool.getUserId();
      if (!userid) {
        self.$message.error("");
        return;
      }
      if (self.dialog.reject_msg.id) {
        self.loading.reject = true;
        self
          .axios({
            method: "post",
            url: this.$store.state.api.borrowApply.approStatus,
            data: {
              gcid: "0100099",
              userid: userid,
              params: {
                id: self.dialog.reject_msg.id,
                status: 2, //0 1 2
                approvalIdea: self.dialog.reject_msg.content
              }
            }
          })
          .then(response => {
            //loading
            self.loading.reject = false;
            const { data } = response;
            if (data.status.code == 200) {
              self.$alert("", "", {
                callback: action => {
                  self.dialog.reject = false;
                  self.dialog.detail = false;
                  self.table.currentPage = 1;
                  self.getList();
                }
              });
            }
          })
          .catch(error => {
            self.loading.reject = false;
          });
      }
    },

    /**---------- */
    submitAdopt() {
      const self = this;
      const userid = tool.getUserId();
      if (!userid) {
        self.$message.error("");
        return;
      }
      if (self.dialog.adopt_msg.id) {
        self.loading.adopt = true;
        self
          .axios({
            method: "post",
            url: this.$store.state.api.borrowApply.approStatus,
            data: {
              gcid: "0100099",
              userid: userid,
              id: self.dialog.adopt_msg.id,
              status: 1, //0 1 2
              approvalIdea: self.dialog.adopt_msg.content
            }
          })
          .then(response => {
            //loading
            self.loading.adopt = false;
            const { data } = response;
            if (data.status.code == 200) {
              self.$alert("", "", {
                callback: action => {
                  self.dialog.adopt = false;
                  self.dialog.detail = false;
                  self.table.currentPage = 1;
                  self.getList();
                }
              });
            }
          })
          .catch(error => {
            self.loading.adopt = false;
          });
      }
    },

    /**---------- */
    tableRowClick(row) {
      const self = this;
      if (row && row.id) {
        const id = row.id;
        self.dialogType = "detail";
        self.openDetail(id);
      }
    },

    /**---------- */
    isButtonAuthor(id) {
      const self = this;
      const userInfo = tool.session.get("userInfo");
      if (!userInfo) {
        self.$router.replace("/login");
      } else {
        const systemResourcesList = userInfo.systemResourcesList;
        if (systemResourcesList) {
          const authorization = systemResourcesList;
          const author = authorization.filter(
            item => item.zIndex == 3 && item.id == id
          );
          if (author.length > 0) {
            return true;
          } else {
            return false;
          }
        }
      }
    }
  },
  created() {
    const self = this;
    self.getList();
  }
};
</script>

