    /**---------- */
    closeContractAll() {
      this.dialog.contract1 = false;
      this.dialog.contract2 = false;
      this.dialog.contract3 = false;
      this.dialog.contract4 = false;
    },

    /**-----1----- */
    openContract1(id, currentItem) {
      this.currentItem = currentItem;
      this.creatContract(id, currentItem.apartmentId);
      this.currentItemId = id;
      this.dialog.contract1 = true;
      this.dialog.contract2 = false;
      this.dialog.contract3 = false;
      this.dialog.contract4 = false;
    },
    /**-----1----- */
    closeContract1() {
      this.dialog.contract1 = false;
      this.dialog.contract1_source_data.signTime = new Date();
    },

    /**-----2----- */
    openContract2() {
      this.dialog.contract1 = false;
      this.dialog.contract2 = true;
      this.dialog.contract3 = false;
      this.dialog.contract4 = false;
