    /**---------- */
    async creatContract(id, apartmentId) {
      const self = this;
      self.loading.contract1 = true;
      //
      const { data: basePartner } = await self.getBasePartner(apartmentId);
      if (basePartner.status.code == 200) {
        self.dialog.contract1_source_data.basePartner = basePartner.result.list;
        if (basePartner.result.list.length > 0) {
          self.dialog.contract1_source_data.basePartner_id =
            basePartner.result.list[0].id;
        }

        //
        const { data: creatContract } = await self.getCreatContract(id);
        if (creatContract.status.code == 200) {
          self.dialog.contract1_source_data.creatContract =
            creatContract.result;
          self.showCreatContract(creatContract.result);

          const contractId = creatContract.result.id; //id
          if (contractId) {
            self.dialog.contract1_source_data.contractId = contractId;
            //
            const { data: ratePriceList } = await self.getRatePriceList(
              contractId
            );
            if (ratePriceList.status.code == 200) {
              const { list } = ratePriceList.result;
              self.dialog.ratePriceList = list;
            }
          }
        }
      }
      self.loading.contract1 = false;
    },

    /**---------- */
    showCreatContract(contract) {
      const self = this;

      //
      let borrowApply = self.table.tbody.filter(item => {
        return item.id == self.currentItemId;
      });
      if (borrowApply.length > 0) {
        borrowApply = borrowApply[0];
      }

      self.resetCreatContract();
      //1
      self.dialog.contract1_source_data.basePartnerName +=
        "" + contract.apartmentName;
      self.dialog.contract1_msg.apartmentName += "" + contract.apartmentName;
      self.dialog.contract1_msg.apartmentAddress +=
        "" + contract.apartmentAddress;
      self.dialog.contract1_msg.apartmentPerson +=
        "" + contract.apartmentPerson;
      self.dialog.contract1_msg.apartmentPhone +=
        "" + contract.apartmentPhone;
      self.dialog.contract1_msg.borrowType +=
        "" + (contract.borrowType == 1 ? "" : "");
      self.dialog.contract1_msg.template += "";
      self.dialog.contract1_msg.financeName += "" + contract.financeName;
      self.dialog.contract1_msg.lender += "" + contract.financePerson;
      self.dialog.contract1_msg.financePerson += "" + contract.financePerson;
      self.dialog.contract1_msg.financeAddress +=
        "" + contract.financeAddress;
      self.dialog.contract1_msg.financePhone += "" + contract.financePhone;
      self.dialog.contract1_msg.signDate += "" + contract.signDate;
      self.dialog.contract1_msg.no += "" + contract.no;
      self.dialog.contract1_msg.cnPrice += "" + contract.cnPrice;
      self.dialog.contract1_msg.term +=
        "" + contract.beginDate + "" + contract.endDate;
      self.dialog.contract1_msg.yearLimit = "" + contract.yearLimit + "";
      self.dialog.contract1_msg.repayDate += "" + contract.repayDate + "";
      self.dialog.contract1_msg.yearRate += "" + contract.yearRate + "%";
      self.dialog.contract1_msg.amoutMonth += "" + contract.amoutMonth + "";
      self.dialog.contract1_msg.payeeName += "" + contract.payeeName;
      self.dialog.contract1_msg.bank += "" + contract.bank;
      self.dialog.contract1_msg.bankAccount += "" + contract.bankAccount;

      //2
      self.dialog.contract2_msg.applicantCompany +=
        "" + contract.apartmentName;
      self.dialog.contract2_msg.applicationType +=
        "" + (contract.borrowType == 1 ? "" : "");
      self.dialog.contract2_msg.quota += "" + contract.price;
      self.dialog.contract2_msg.paymenTime += "" + borrowApply.expectDate;
      self.dialog.contract2_msg.purpose += "" + borrowApply.remark; // contract.moneyPurpose;
      self.dialog.contract2_msg.houseAddress = contract.apartmentAddress;
      self.dialog.contract2_msg.quota2 = contract.price;
      self.dialog.contract2_msg.name = contract.payeeName;
      self.dialog.contract2_msg.phone = contract.payeePhone;
      self.dialog.contract2_msg.bankName = contract.bank;
      self.dialog.contract2_msg.bankAccount = contract.bankAccount;

      //3
      self.dialog.contract3_msg.applicantCompany +=
        "" + contract.apartmentName;
      self.dialog.contract3_msg.houseAddress += "" + contract.houseAddress;
      self.dialog.contract3_msg.houseNumber =
        "" + contract.houseNumber + "";
      self.dialog.contract3_msg.moneyPurpose += "" + contract.moneyPurpose;
      self.dialog.contract3_msg.quota += "" + contract.price;
      self.dialog.contract3_msg.yearRate += "" + contract.yearRate + "%";
      self.dialog.contract3_msg.term +=
        "" + contract.beginDate + "" + contract.endDate;

      //
      self.dialog.contract4_msg.applicantCompany1 +=
        "" + contract.financeName;
      self.dialog.contract4_msg.applicantCompany2 +=
        "" + contract.apartmentName;
      self.dialog.contract4_msg.beginDate += "" + borrowApply.expectDate;
      self.dialog.contract4_msg.price += "" + contract.price + "";
      self.dialog.contract4_msg.term +=
        "" + contract.beginDate + "" + contract.endDate;
      self.dialog.contract4_msg.yearLimit = "" + contract.yearLimit + "";
      self.dialog.contract4_msg.moneyPurpose += "" + contract.moneyPurpose;
      self.dialog.contract4_msg.paymentType += "";
    },

    /**---------- */
    getCreatContract(id, gcid) {
      const self = this;
      const userid = tool.getUserId();
      if (!userid) {
        self.$message.error("");
        return;
      }
      return this.axios({
        method: "post",
        url: this.$store.state.api.borrowApply.creatContract,
        data: {
          gcid: "0100099",
          userid: userid,
          params: {
            id: id,
            gcid: gcid
          }
        }
      });
    },

    /**---------- */
    getBasePartner(apartmentId) {
      return this.axios({
        method: "post",
        url: this.$store.state.api.borrowApply.getBasepartnerList,
        data: {
          params: {
            apartmentId: apartmentId
          }
        }
      });
    },

    /**---------- */
    getRatePriceList(contractId) {
      const self = this;
      const userid = tool.getUserId();
      if (!userid) {
        self.$message.error("");
        return;
      }
      return this.axios({
        method: "post",
        url: this.$store.state.api.borrowApply.getRatePriceList,
        data: {
          gcid: "0100099",
          userid: userid,
          params: {
            contractId: contractId
          }
        }
      });
    },

    /**-----PDF----- */
    getCreatPDF(contractId) {
      return this.axios({
        method: "post",
        url: this.$store.state.api.picture.getFile,
        data: {
          params: {
            bizId: contractId,
            bigType: 6,
            smallType: 1
          }
        }
      });
    },

    /**---------- */
    async changeBasePartner() {
      const self = this;
      self.loading.contract1 = true;
      const id = self.dialog.contract1_source_data.creatContract.applyId;
      //const gcid = self.dialog.contract1_source_data.basePartner_gcid;
      const { data: creatContract } = await self.getCreatContract(id, gcid);
      if (creatContract.status.code == 200) {
        self.dialog.contract1_source_data.creatContract =
          creatContract.result.list;
        self.showCreatContract(creatContract.result.list);
      }
      self.loading.contract1 = false;
    },

    /**---------- */
    getBorrowApplyList() {
      const self = this;
      //loading
      self.loading.all = true;
      self
        .axios({
          method: "post",
          url: this.$store.state.api.borrowApply.getList,
          data: {
            minPrice: self.formInline.minPrice,
            maxPrice: self.formInline.maxPrice,
            pageNo: self.currentPage,
            likeName: self.formInline.likeName,
            pageSize: self.pageSize,
            startDate: self.formInline.date[0],
            endDate: self.formInline.date[1],
            status: 1 //0 1 2 3
          }
        })
        .then(response => {
          //loading
          self.loading.all = false;
          const { data } = response;
          if (data.status.code == 200) {
            self.table.tbody = data.result.list;
            self.totalRecord = data.result.totalRecord;
          }
        })
        .catch(error => {
          self.loading.all = false;
        });
    },

    /**---------- */
    currentChange(currentPage) {
      const self = this;
      self.currentPage = currentPage;
      self.getBorrowApplyList();
    },

    /**---------- */
    sizeChange(pageSize) {
      const self = this;
      self.currentPage = 1;
      self.pageSize = pageSize;
      self.getBorrowApplyList();
    },

    /**---------- */
    resetCreatContract() {
      const self = this;
      const contract1_msg = {
        borrowType: "", //
        template: "", //
        apartmentName: "", //
        apartmentAddress: "", //
        apartmentPerson: "", //
        apartmentPhone: "", //
        financeName: "", //
        lender: "", //
        financePerson: "", //
        financeAddress: "", //
        financePhone: "", //
        signDate: "", //
        no: "", //
        cnPrice: "", //
        term: "", //
        yearLimit: "", //
        repayDate: "", //
        yearRate: "", //
        amoutMonth: "", //
        payeeName: "", //
        bank: "", //
        bankAccount: "" //
      };

      const contract2_msg = {
        applicantCompany: "", //
        applicationType: "", //
        quota: "", //
        paymenTime: "", //
        purpose: "", //
        houseAddress: "", //
        quota2: "", //
        name: "", //
        phone: "", //
        bankName: "", //
        bankAccount: "" //
      };

      const contract3_msg = {
        applicantCompany: "", //
        houseAddress: "", //
        houseNumber: "", //
        moneyPurpose: "", //
        quota: "", //
        yearRate: "", //
        term: "" //
      };

      const contract4_msg = {
        applicantCompany1: "", // 
        applicantCompany2: "", // 
        beginDate: "", //
        price: "", //
        term: "", //
        yearLimit: "", //
        moneyPurpose: "", //
        paymentType: "" //
      };

      self.dialog.contract1_msg = contract1_msg;
      self.dialog.contract2_msg = contract2_msg;
      self.dialog.contract3_msg = contract3_msg;
      self.dialog.contract4_msg = contract4_msg;

      self.dialog.contract1_source_data.basePartnerName = "";
    },

    /**---------- */
    sureCreatContract() {
      const self = this;
      const userid = tool.getUserId();
      if (!userid) {
        self.$message.error("");
        return;
      }
      //
      if (self.loading.contract1) {
        return;
      }
      if (!self.dialog.contract1_source_data.basePartner_id) {
        self.$message.error("");
        return;
      }
      if (!self.dialog.contract1_source_data.signTime) {
        self.$message.error("");
        return;
      }
      self.loading.contract4 = true;
      self
        .axios({
          method: "post",
          url: self.$store.state.api.borrowApply.saveContract,
          data: {
            gcid: "0100099",
            userid: userid,
            params: {
              contractId: self.dialog.contract1_source_data.contractId,
              signDate: self.dialog.contract1_source_data.signTime,
              apartmentId: self.dialog.contract1_source_data.basePartner_id
            }
          }
        })
        .then(response => {
          self.loading.contract4 = false;
          const { data } = response;
          if (data.status.code == 200) {
            self.closeContractAll();
            self.getBorrowApplyList();
            self.$alert("", "");
          }
        })
        .catch(error => {
          self.loading.contract4 = false;
        });
    },

    /**---------- */
    tableRowClick(row) {
      const self = this;
      if (row && row.id) {
        const id = row.id;
        self.openDetail(id);
      }
    },

    /**---------- */
    isButtonAuthor(id) {
      const self = this;
      const userInfo = tool.session.get("userInfo");
      if (!userInfo) {
        self.$router.replace("/login");
      } else {
        const systemResourcesList = userInfo.systemResourcesList;
        if (systemResourcesList) {
          const authorization = systemResourcesList;
          const author = authorization.filter(
            item => item.zIndex == 3 && item.id == id
          );
          if (author.length > 0) {
            return true;
          } else {
            return false;
          }
        }
      }
    }
  },
  created() {
    var self = this;
    self.getBorrowApplyList();
  }
};
</script>

<style scoped>
.el-row {
  margin-bottom: 20px;
}
.el-row.no-margin-bottom {
  margin-bottom: 0px;
}
.el-row.search {
  background: #fff;
  padding: 20px;
}
.el-button--primary {
  width: 100%;
  background-color: #188ff0;
  border-color: #188ff0;
  border-radius: 0px;
}
.el-button--primary.is-plain {
  background-color: #fff;
  border-color: #188ff0;
  border-width: 1px;
  color: #188ff0;
}
/deep/ .el-textarea__inner {
  padding: 10px 15px;
  min-height: 100px !important;
}
/deep/ .el-input__inner {
  border-radius: 0px;
}
/deep/ .el-button--primary span {
  font-size: 14px;
}
/deep/ .el-table tr {
  background: #f7f7f7;
}
/deep/ .el-table--striped .el-table__body tr.el-table__row--striped td {
  background: #ffffff;
}
/deep/ .el-table th > .cell {
  font-size: 14px;
}
/deep/ .el-table thead tr {
  background-color: #ffffff;
  color: #2e3445;
}
/deep/ .el-pagination {
  padding: 0px;
  text-align: right;
}
/deep/ .el-pagination.is-background .el-pager li.active {
  background-color: #188ff0;
}
/deep/ .el-pagination.is-background .el-pager li:hover {
  color: #188ff0;
}
/deep/ .el-form-item {
  margin: 0px;
  border: 1px solid #dcdfe6;
}
/deep/ .el-form-item .el-input__inner {
  border: 0px;
}
/deep/ .noMarginRight > .el-form-item__content {
  width: 100%;
}
/deep/ .el-form--inline .el-form-item {
  margin-right: 10px;
}
/deep/ .el-form--inline .el-form-item__label {
  padding-left: 12px;
  padding-right: 0px;
}
/deep/ .el-date-editor .el-range-input {
  width: 80px;
}
/deep/ .el-date-editor .el-range-separator {
  width: 30px;
}
/deep/ .el-dialog__header {
  border-bottom: 1px solid #f2f6f7;
}
/deep/ .el-dialog__title {
  color: #188ff0;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  font-size: 14px;
  color: #188ff0;
}
/deep/ .el-input.is-disabled .el-input__inner {
  background-color: #f0eeee;
  border-color: #e6eced;
  color: #404040;
}
/deep/ .noBorder .el-input__inner {
  border: 0px;
  padding-right: 0px;
}
/deep/ .el-table {
  min-height: calc(100vh - 275px);
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  font-size: 24px;
}
.el-input.is-disabled {
  position: relative;
}
.el-input.is-disabled::before {
  position: absolute;
  left: 0px;
  top: 0px;
  content: "";
  color: #f2082e;
}
.finance-state {
  display: inline-block;
  color: #fff;
  width: 70px;
  padding-right: 10px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background: url(../../../static/image/icon_state_green.png) left top no-repeat;
  background-size: 70px 30px;
}
.title {
  color: #188ff0;
  padding-left: 20px;
  position: relative;
  height: 20px;
  line-height: 20px;
  margin-bottom: 20px;
  font-weight: normal;
  font-size: 14px;
}
.title::before {
  content: "";
  display: block;
  width: 3px;
  height: 18px;
  background-color: #188ff0;
  position: absolute;
  top: 1px;
  left: 0px;
}
.title.no-mark {
  padding-left: 0px;
}
.title.no-mark::before {
  content: none;
}
.detail-title {
  font-size: 14px;
  color: #000;
  margin-right: 20px;
}
.detail-item {
  font-size: 12px;
  color: #777;
}
.detail-item.red {
  color: #f2082e;
}
.detail-item.green {
  color: #01be6a;
}
.division {
  border-style: solid;
  border-width: 1px;
  border-color: #dfe4e6;
  margin: 0px;
  margin-bottom: 20px;
}
.detail-img {
  display: block;
  border: 0px;
  width: 85%;
  height: 85%;
  margin: 0 auto;
}
.error-message {
  font-size: 14px;
  color: #f2082e;
  margin-bottom: 10px;
}
.error-success {
  font-size: 14px;
  color: #188ff0;
  margin-bottom: 10px;
}
.progress-bg-gray {
  width: 100%;
  height: 10px;
  background: #ebf0f2;
  border-radius: 5px;
  position: relative;
}
.progress-bg-active {
  width: 0%;
  height: 10px;
  background: #188ff0;
  border-radius: 5px;
  position: absolute;
  left: 0px;
  top: 0px;
}
.progress-bg-active.progress-bg-active1 {
  width: 25%;
}
.progress-bg-active.progress-bg-active2 {
  width: 50%;
}
.progress-bg-active.progress-bg-active3 {
  width: 75%;
}
.progress-bg-active.progress-bg-active4 {
  width: 100%;
}
.progress-tips-con {
  width: 100%;
  position: absolute;
  left: 0px;
  top: -40px;
  margin: 0px;
}
.progress-tips-item {
  position: relative;
}
.progress-tips-item::after {
  position: absolute;
  content: "";
  left: 50%;
  margin-left: -15px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  background: #ebf0f2;
  color: #666;
  font-size: 14px;
}
.progress-tips-con .progress-tips-item:nth-of-type(1)::after {
  content: "1";
}
.progress-tips-con .progress-tips-item:nth-of-type(2)::after {
  content: "2";
}
.progress-tips-con .progress-tips-item:nth-of-type(3)::after {
  content: "3";
}
.progress-tips-con .progress-tips-item:nth-of-type(4)::after {
  content: "4";
}
.progress-tips-item.active::after {
  background: #188ff0;
  color: #fff;
}
.progress-tips-item .progress-tips-item-text {
  color: #999999;
  text-align: center;
  font-size: 14px;
  padding-bottom: 7px;
}
.progress-tips-item.active .progress-tips-item-text {
  color: #188ff0;
}
.margin-bottom10 {
  margin-bottom: 10px;
}
.receivables-table {
  width: 100%;
  border-color: #e6eced;
  border-style: solid;
  border-collapse: collapse;
}
.receivables-table td {
  padding: 10px 20px;
  font-size: 14px;
  color: #000;
}
.receivables-table td.receivables-table-gray {
  color: #777;
}
.receivables-table tr:nth-of-type(even) {
