}
/deep/ .timeTime {
}
.contractBody {
  height: calc(100vh - 340px);
  overflow-x:hidden;
  overflow-y:scroll;
}
.image_box figure {
  width: 100px;
  height: 94px;
  float: left;
  overflow: hidden;
  margin: 0;
  margin-left: 5px;
}
.image_box::after{
  content: '';
  display: block;
  clear: both;
}
/deep/ .image_box img{
  width: 100px;
  height: 60px  ;
}
/deep/ .image_box figcaption{
  text-align: center;
}
</style>



