<script>
  import tool from "../../../static/js/tool.js";
  export default {
    name: "AlreadyCollect",
    data() {
      return {
        searchMsgbtn: false,
        totalRecord: 0,//
        ContractPlanBtn: false, // 
        currentItem:{}, //
        imgList:[], //
        searchMsg: {
