          detail_msg: {
            id: "",
            tabIndex: "first", //tabs
            no: "", //
            borrowType: "", //
            price: "", //
            yearLimit: "", //
            datetime: "", //
            state: "", //
            stateText: "", //
            firstImage: [], //
