            thirdImage: [], //
            fourthImage: [] //
          },
          reject: false, //
          reject_msg: {}, //
          adopt: false, //
          adopt_msg: {} //
        },
        paymentRejectOption: {
          btn: false, //
          msg: '', // 
          currentItem: ''// 
        },
        loading: {
          all:false,  // loading
          back:false, // loading
          viewDetail: false ,// 
        },
        waitBackBtn: false,  //  
        formInline: {
          user: "",
          region: ""
        },
        currentPage: 1, //
        pageSize: 5,
        //
        screening: {
          areaValue: "", ///
          area: [] ///
        },
        //
        table: {
          thead: [],
          tbody: [],
