import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

//api
import api from "./serverApi.js"

import apartmentRole from "./setting/apartmentRole.js"  //-
import roleManger from "./setting/roleManger.js"  //-
import staffManger from "./setting/staffManger.js"  //-
import corporateBulletin from "./setting/corporateBulletin.js"  //-
import dictManger from "./setting/dictManger.js"  //-
import partners from "./cooperation/partners.js"  //-

const store = new Vuex.Store({
    state: { api },
    modules: {
        apartmentRole,
        roleManger,
        staffManger,
        corporateBulletin,
        dictManger,
        partners
    }
});

export default store;



